import axios, { AxiosRequestConfig, AxiosResponse, AxiosError } from 'axios';
let errorCallback: (message: string) => void = () => {};

const useProd = true;
export const server = useProd ? 'https://api.oxfordchess.co.uk/' : 'http://localhost:8080';

axios.defaults.baseURL = server;


export function GET(options: AxiosRequestConfig): Promise<any> {
	options = Object.assign(options, {
		method: 'GET'
	});
	return axios(options)
		.then((res: AxiosResponse<any>) => res.data)
		.catch((e: AxiosError) => {
			console.error(e);
			if (e.response) errorCallback([e.response.status, e.response.statusText, ':', '/' + e.response.config.url?.split('/').pop()].join(' '));
			throw e;
		});
}

export function POST(options: AxiosRequestConfig): Promise<any> {
	options = Object.assign(options, {
		method: 'POST',
		headers: {
			'Content-Type': 'application/json'
		}
	});
	return axios(options)
		.then((res: AxiosResponse<any>) => res.data)
		.catch((e) => {
			if (e && e.response && e.response.data) errorCallback(e.response.data);
			throw e;
		});
}

export function setErrorCallback(c: (message: string) => void) {
	errorCallback = c;
}