import React, { useEffect, useState, useMemo } from 'react';
import './App.css';
import { TournamentProps, TournamentStatus, Player, PairingObject } from './interfaces';
import ChessCom from './chess/ChessCom';
import { GET } from './utils/requests';
import { AiOutlineLeft } from 'react-icons/ai';
import * as icons from './assets/icons/';
import * as regexes from './utils/regexes';

export default function Tournament(props: TournamentProps) {

    const id = props.match.params.id
    const [loaded, setLoaded] = useState(false);
    const [state, setState] = useState({});
    const [settings, setSettings] = useState({});
    const [status, setStatus] = useState({} as TournamentStatus);
    const [players, setPlayers] = useState([] as Player[]);
    const [pairings, setPairings] = useState([] as PairingObject[]);

    const playerDict: Map<string, Player> = useMemo(() => {
        if (!players.length) return new Map();
        let dict = new Map();
        for (let p of players) dict.set(p.id, p);
        return dict;
    }, [players]);

    useEffect(() => {
        Promise.all([
            GET({ url: '/tournament/' + id + '/fetchPlayers'})
                .then(setPlayers)
                .catch(() => {}),
            GET({ url: '/tournament/' + id + '/getPairings'})
                .then(setPairings)
                .catch(() => {}),
            GET({ url: '/tournament/' + id + '/getStatus'})
                .then(setStatus)
                .catch(() => {})
        ]).then(() => setLoaded(true)) /*
        GET({ url: '/tournament/' + id + '/getSettings'})
            .then(setSettings);
        GET({ url: '/tournament/' + id + '/getState'})
            .then(setState);*/

    }, [setPairings, setPlayers]);

    const round = status.round || 0;

    const [expanded, setExpanded] = useState('');

    useEffect(() => {
        if (!round) return;
        let histories = new Array(round + 1);
        for (let i = 1; i < round + 1; i++) histories[i] = {
            match: 0,
            game: 0
        };
        playerDict.set('bye', {
            id: 'bye',
            firstName: 'BYE',
            histories
        } as any as Player);
    }, [playerDict, round]);

    if (!loaded) return null;

    return (

        <div id='main'>
            <div className='container'>
                    <h1>{status.name}</h1>

                    <section>
                        <h2>Pairings</h2>
                        <table className='download-table full-width'>
                            <thead>
                                <tr>
                                    <td style={{width: '30%'}}>White</td>
                                    <td style={{width: '12.5%'}}></td>
                                    <td style={{width: '5%'}}></td>
                                    <td style={{width: '5%'}}></td>
                                    <td style={{width: '5%'}}></td>
                                    <td style={{width: '12.5%'}}></td>
                                    <td style={{width: '30%'}}>Black</td>
                                </tr>
                            </thead>
                            <tbody>                                
                                {pairings.map((row) => {
                                    let id = [row[0].trim(), row[1].trim()].join('.')
                                    let w = playerDict.get(row[0].trim());
                                    let b = playerDict.get(row[1].trim());
                                    let link = Object.values(row[2]).find(v => v);
                                    let matches = link?.match(regexes.chessCom);
                                    if (!matches) link = '';
                                    else link = 'https://www.chess.com/club/matches/live/' + matches[1];
                                    return (
                                        <>
                                            <tr>
                                                <td><div className='playerName'>{w?.firstName}</div></td>
                                                <td style={{padding: 0}}>
                                                    {link ? <a className='button' href={link.trim()} target='_blank' rel='noopener noreferrer'>
                                                        <img className='icon' src={icons.watch} alt={'Watch games'}/>
                                                    </a> : null}
                                                </td>                                                
                                                <td className={'bold'}>{w?.histories[round]?.game}</td>
                                                <td>-</td>
                                                <td className={'bold'}>{b?.histories[round]?.game}</td>
                                                <td style={{padding: 0}}>
                                                    {link ? <div className='button' onClick={() => setExpanded(expanded ? '' : id)}>
                                                        <AiOutlineLeft style={{
                                                            transform: expanded ? 'rotate(-90deg)' : 'rotate(0deg)'
                                                        }}/>
                                                    </div> : null}
                                                </td>
                                                <td><div className='playerName'>{b?.firstName}</div></td>
                                            </tr>
                                            {link && expanded === id? <tr>
                                                <td colSpan={100}>
                                                    {row[2].chessComLink || row[2].chessComTeamLink ?
                                                        row[2].chessComTeamLink ?
                                                            <ChessCom matchURL={row[2].chessComTeamLink} /> :
                                                            null :
                                                        null
                                                    }
                                                </td>
                                            </tr> : null}
                                        </>
                                    )
                                })}
                            </tbody>
                        </table>
                    </section>


            </div>
        </div>
    );
}
